import { Component, OnInit } from '@angular/core';
import { Observable, of } from 'rxjs';
import { IProjectsModel } from '../../shared/models/ProjectsModel';
import { ProjectsDataStoreService } from '../../shared/data-store/projects-data-store.service';
import { tap, catchError } from 'rxjs/internal/operators';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {

  public allProjects: IProjectsModel[];
  public isLoading: boolean;
  public showError: boolean;

  constructor(private projectsService: ProjectsDataStoreService) { 
    this.isLoading = false;
    this.showError = false;
    this.allProjects = [];
  }

  ngOnInit() {
    this.getAllProjects();
  }

  public getAllProjects(){
    this.isLoading = true;
    this.showError = false;
    this.projectsService.getAllProjects().subscribe((projects: IProjectsModel[])=> {
      this.allProjects = projects;
      this.isLoading = false;
    },()=> {
      this.isLoading = false;
      this.showError = true;
    });    
  }

  public openNewPage(link: string) {
    window.open(link,'_blank');
  }

}
